.message-item {
    align-items: flex-start;
    padding: 3px 10px;
    .message-item-text {
        border-radius: 4px;
        margin-top: 5px;
        height: auto;;
        color: #222;
        padding: 5px 10px;
        word-wrap: break-word;
        word-break: break-all;
        max-width: 60%;
    }
    .message-item-notice {
        color: #aaa;
    }
}
.message-item[role='self'] {
    .message-item-text {
        text-align: end;
        background-color: #98ea70;
    }
    
}
.message-item[role='other'] {
    .message-item-text {
        background-color: #efefef;
    }
    
}