.main-tabs {
    .ant-tabs-nav {
        margin-bottom: 0;
        .ant-tabs-nav-wrap{
            .ant-tabs-nav-list {
                align-self: stretch;
                flex-grow: 1;
                .ant-tabs-tab {
                    flex-grow: 1;
                    justify-content: center;
                }
                .ant-tabs-tab-active {
                  .ant-typography {
                    color: #1890ff!important;
                  }
                }
            }
        }   
    }
}
.main-tabs p {
    margin: 0;
  }
  .main-tabs> .ant-tabs-card .ant-tabs-content {
    height: 120px;
    margin-top: -16px;
  }
  .main-tabs> .ant-tabs-card .ant-tabs-content > .ant-tabs-tabpane {
    padding: 16px;
    background: #fff;
  }
  .main-tabs> .ant-tabs-card > .ant-tabs-nav::before {
    display: none;
  }
  .main-tabs> .ant-tabs-card .ant-tabs-tab,
  [data-theme='compact'] .main-tabs> .ant-tabs-card .ant-tabs-tab {
    background: transparent;
    border-color: transparent;
  }
  .main-tabs> .ant-tabs-card .ant-tabs-tab-active,
  [data-theme='compact'] .main-tabs> .ant-tabs-card .ant-tabs-tab-active {
    background: #fff;
    border-color: #fff;
  }
  #components-tabs-demo-card-top .code-box-demo {
    padding: 24px;
    overflow: hidden;
    background: #f5f5f5;
  }
  [data-theme='compact'] .main-tabs> .ant-tabs-card .ant-tabs-content {
    height: 120px;
    margin-top: -8px;
  }
  [data-theme='dark'] .main-tabs> .ant-tabs-card .ant-tabs-tab {
    background: transparent;
    border-color: transparent;
  }
  [data-theme='dark'] #components-tabs-demo-card-top .code-box-demo {
    background: #000;
  }
  [data-theme='dark'] .main-tabs> .ant-tabs-card .ant-tabs-content > .ant-tabs-tabpane {
    background: #141414;
  }
  [data-theme='dark'] .main-tabs> .ant-tabs-card .ant-tabs-tab-active {
    background: #141414;
    border-color: #141414;
  }