.page-wrapper {
    min-height: 100%;
    background: #1890ff;
    .page-header {
        height: 64px;
        color: #fff!important;
    }
    .page-main {
        
        .page-sider {
            background-color: #fff;
        }
        .page-content {
            border-left: 1px solid #e2e2e2;
            border-right: 1px solid #e2e2e2;
            background-color: #eee;
        }
    }
    
}