.page-wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    .page-main {
        flex-grow: 1;
        .page-content {
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            .ant-tabs-content {
                height: 100%;
                .ant-tabs-tabpane {
                    display: flex;
                    flex-direction: column;
                }
            }
        }
        .main-tabs {
            flex-grow: 1;
        }
    }
}
.messages-wrap {
    height: 100%;
    display: flex;
    flex-direction: column;
}
.message-top-bar {
    background-color: #fff;
    padding: 0 15px;
    height: 40px;
    border-bottom: 1px solid #ddd;
    .message-top-bar-name {
        margin-bottom: 0;
    } 
}
.message-list {
    background-color: #fff;
    overflow-y: scroll;
    flex-grow: 1;
}
.editor-wrap {
    height: 150px;
    position: relative;
    flex-grow: 0;
    flex-shrink: 0;
    .editor-send {
        position: absolute;
        right: 10px;
        bottom: 10px;
        z-index: 1000;
    }
}

